<template>
    <div  :key="find()">
    <div v-if="(x)%2" >
        <div class="outer">
        <!-- <h1 style="colour:'blue">tanuj</h1> -->
        <img src="@/assets/cross.png"  v-on:click="close()" class="small" >
        <img v-bind:class="{myclass:true}" :src="getImgUrl(bg)" alt="close" >
        <!-- <h1> {{bg}} </h1> -->
        </div>
    </div>
    </div>

</template>
<script>
export default {
    name:'bigImage',
    props:[
        'bg','show'
    ],
    data(){
        return {
            x:this.show
        }
    },
    methods:{
            find()
            {
                this.x=this.show
            },
           getImgUrl(pet) {
            var images = require.context('../assets/', false, /\.png$/)
            return images('./' + pet + ".png")

            },
            updateParentShow(){
                this.$emit('updateshow')
            },
            close(){
                console.log('show',this.show)
                    this.x++
                    this.updateParentShow();

            },
    },
}
</script>
<style scoped>
.outer{
    display:flex;
    flex-direction: column;
    z-index: 1;
    padding:5%;
    width:60%;
    margin-left:20%;
    min-height:100vh;
    align-content: center;
    justify-content: center;
    position:absolute;


}
.myclass{
        /* position:absolute; */
        z-index: 1;
        opacity:1;
        border-radius:5%;
        /* background: blue; */
        /* max-width:800px; */
        /* filter:grayscale(0%); */
    }
.small{
    height:50px;
    width:50px;
    align-self:flex-end;


}
</style>